.ContactDetails {
  .BackLink {
    margin-bottom: 20px;
  }
  .ContactPreviewTransfer {
    display: flex;
    width: 100%;

    .ContactPreview,
    .TransferFund {
      width: 50%;
    }
    & > a {
      display: block;
      position: absolute;
      padding-top: 10px;
      padding-left: 10px;
    }
  }
}
@media (max-width: $break-mobail) {
  .ContactDetails {
    .ContactPreviewTransfer {
      flex-direction: column;
      // align-items: center;
      & > .ContactPreview {
        width: 100%;
      }
      div.TransferFund {
        width: 100%;
      }
    }
  }
}
